import Header from "./components/Header";
import AuthContext, { AuthProvider } from "./contex/AuthContex";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Register from "./pages/Register";

import React, { useContext } from 'react';
import { Route, Routes } from 'react-router-dom';
import PrivateRoute from "./utlis/PrivateRoutes";
import Logout from "./utlis/Logout";
import NotFound from "./pages/NotFound ";
import Profile from "./pages/Profile";
import Chat from "./pages/Chat";
import Dialog from "./pages/Dialog";

function App() {
  return (
    <>
      <AuthProvider>
      <Header />
        <Routes>
          <Route path="/register" element={<Register />} />
          <Route path="/login" element={<Login />} />
          <Route path="/logout" element={<Logout />} />
          <Route path="/" element={<PrivateRoute><Home /></PrivateRoute>} />
          <Route path="/chat" element={<PrivateRoute><Chat /></PrivateRoute>} />
          <Route path="/dialog/:id" element={<PrivateRoute><Dialog /></PrivateRoute>} />
          <Route path="/:username" element={<PrivateRoute><Profile /></PrivateRoute>} />
          <Route path="*" element={<NotFound />} /> {/* Catch-all for 404 */}
        </Routes>
      </AuthProvider>
    </>
  );
}

export default App;
