import React, { useContext, useState } from 'react';
import AuthContext from '../contex/AuthContex';
import { Link } from 'react-router-dom';

function Logout() {
  const { logoutUser,user } = useContext(AuthContext);
  
  useState(()=>{
    console.log(user)
    if(!user){
        logoutUser()
    }
  },[])
  


  return (
    <div className="flex flex-col justify-center items-center h-screen bg-gray-100">
      <h2 className="mb-6 text-xl font-semibold text-gray-800">Вы уверены, что хотите выйти?</h2>
      <div className="flex gap-4">
        <button 
          className="px-6 py-2 text-white bg-red-600 rounded-lg hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-400"
          onClick={logoutUser}
        >
          Да, выйти
        </button>
        <Link 
          className="text-lg font-medium text-teal-600 hover:underline focus:outline-none"
          to='/'
        >
          Нет...
        </Link>
      </div>
    </div>
  );
}

export default Logout;
