import React, { useContext, useEffect, useRef, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import AuthContext from '../contex/AuthContex';
import MAIN_URL from '../settings';

function Dialog() {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const { id } = useParams();
  const { authTokens, profile } = useContext(AuthContext);
  const socketRef = useRef(null);
  const messagesEndRef = useRef(null);

  useEffect(() => {
    const fetchMessages = async () => {
      try {
        const res = await fetch(`${MAIN_URL}}/api/messages/${id}/`, {
          headers: { 'Authorization': `Bearer ${authTokens.access}` },
        });
        const data = await res.json();
        setMessages(data.messages || []);
        scrollToBottom();
      } catch (error) {
        console.error('Error fetching messages:', error);
      }
    };

    fetchMessages();
    connectWebSocket();

    return () => {
      if (socketRef.current) {
        socketRef.current.close();
      }
    };
  }, [id, authTokens.access]);

  const connectWebSocket = () => {
    if (socketRef.current) {
      socketRef.current.close();
    }

    socketRef.current = new WebSocket(
      `ws://127.0.0.1:8000/ws/chat/${id}/?token=${authTokens.access}`
    );

    socketRef.current.onopen = () => {
      console.log('Connected to WebSocket');
    };

    socketRef.current.onmessage = (event) => {
      const data = JSON.parse(event.data);
      setMessages((prevMessages) => [...prevMessages, data]);
      scrollToBottom();
    };
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    if (!newMessage.trim()) return;

    socketRef.current.send(
      JSON.stringify({
        sender: profile?.username,
        content: newMessage.trim(),
      })
    );

    setNewMessage('');
  };

  return (
    <main className="w-full max-w-[1040px] m-auto flex gap-[20px] h-screen">
      <div className="flex flex-col gap-[20px] flex-1 pt-[20px] bg-white relative">
        <header className="absolute top-0 right-0 w-full h-[60px] bg-white shadow flex items-center justify-between px-[20px]">
          <div className="flex w-full gap-[10px]">
            <Link to="/chat" className="text-[32px] font-bold">
              {'<'}
            </Link>
            <div className="rounded-full overflow-hidden">
              <img className="max-h-[48px] max-w-[48px] h-full w-full object-cover" 
                   src="https://play-lh.googleusercontent.com/vco-LT_M58j9DIAxlS1Cv9uvzbRhB6cYIZJS7ocZksWRqoEPat_QXb6fVFi77lciJZQ=w526-h296-rw" 
                   alt="User" />
            </div>
            <div className="flex flex-col justify-center">
              <p>{profile?.username}</p>
              <small>Online now</small>
            </div>
          </div>
          <button className="text-[32px] font-bold">...</button>
        </header>

        <div className="py-[70px] h-full overflow-y-auto bg-slate-300 flex flex-col p-4 gap-4 scrollbar-hide">
          {messages?.map((message, index) => (
            <div
              key={index}
              className={`max-w-[75%] p-3 shadow-md rounded-2xl ${
                message.sender === profile?.username
                  ? 'bg-blue-500 text-white self-end'
                  : 'bg-white text-gray-800 self-start'
              }`}
            >
              <p>{message?.content}</p>
              <small className="text-xs block mt-1">
                {message.sender} - {new Date().toLocaleTimeString()}
              </small>
            </div>
          ))}
          <div ref={messagesEndRef} />
        </div>

        <form onSubmit={handleSendMessage} className="flex justify-between p-[10px] items-center absolute bottom-0 left-0 w-full bg-white">
          <input
            type="text"
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            className="w-full h-full py-[10px] focus:outline focus:outline-2 focus:outline-[#FFBE20]"
            placeholder="Напишите сообщение"
          />
          <input type="submit" value="Отправить" />
        </form>
      </div>

      <div className="w-[300px] sticky top-0 h-full pt-[20px]">
        <div className="p-[20px] bg-white rounded-[15px] flex flex-col items-start w-full">
          <button className="text-start w-full px-[10px] py-[5px]">Сначала новые</button>
          <button className="text-start w-full px-[10px] py-[5px]">Сначала старые</button>
          <hr />
          <button className="text-start w-full px-[10px] py-[5px]">Рекомендованные</button>
          <button className="text-start w-full px-[10px] py-[5px]">Сначала популярные</button>
        </div>

        <div className="p-[20px] bg-white rounded-[15px] flex flex-col items-start w-full mt-[20px]">
          <button className="text-start w-full px-[10px] py-[5px]">Лайкнутые посты ❤️</button>
          <button className="text-start w-full px-[10px] py-[5px]">Реакции</button>
        </div>
      </div>
    </main>
  );
}

export default Dialog;
