import React, { useContext, useEffect, useState } from 'react'
import ChatCard from '../components/ChatCard'
import AuthContext from '../contex/AuthContex'
import MAIN_URL from '../settings'

function Chat() {
  const [chats, setChats] = useState([])
  const { authTokens } = useContext(AuthContext)

  useEffect(() => {
    const fetchChats = async () => {
      if (!authTokens) return // Prevent fetch if user is not authenticated
      console.log(authTokens)
      try {
        const response = await fetch(MAIN_URL+'/api/chatrooms/', {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${authTokens.access}`, // Assuming user.token contains the JWT token
            'Content-Type': 'application/json'
          }
        })

        if (!response.ok) {
          throw new Error('Network response was not ok')
        }

        const data = await response.json()
        setChats(data.chat_rooms) // Assuming the response contains chat_rooms data
      } catch (error) {
        console.error("There was an error fetching chat rooms:", error)
      }
    }

    fetchChats()
  }, [authTokens]) // Re-run the effect whenever user changes

  return (
    <>
      <div className="max-w-[1040px] m-auto flex gap-[20px] mt-[20px] h-full">
        <ul className='flex bg-white py-[10px] px-[20px] rounded-[20px] justify-between w-full'>
          <li><button className='px-[36px] py-[10px] bg-[#EDEEF0] rounded-[15px]'>Все</button></li>
          <li><button className='px-[36px] py-[10px]'>Посты</button></li>
          <li><button className='px-[36px] py-[10px]'>Сообщения</button></li>
          <li><button className='px-[36px] py-[10px]'>Аудиозапись</button></li>
          <li><button className='px-[36px] py-[10px]'>Коментарии</button></li>
        </ul>
        <div className='max-w-[300px] w-full'></div>
      </div>

      <main className="w-full max-w-[1040px] m-auto mb-[40px] flex gap-[20px] min-h-screen py-[20px]">
        <div className="flex flex-col gap-[20px] flex-1 pt-[20px]">
          {chats.length > 0 ? (
            chats.map((chat) => (
              <ChatCard key={chat.id} chat={chat} />
            ))
          ) : (
            <p>No chats available</p>
          )}
        </div>

        <div className="w-[300px] sticky top-0 h-full pt-[20px]">
          <div className="p-[20px] bg-white rounded-[15px] flex flex-col items-start w-full">
            <button className="text-start w-full px-[10px] py-[5px]">Сначала новые</button>
            <button className="text-start w-full px-[10px] py-[5px]">Сначала старые</button>
            <hr />
            <button className="text-start w-full px-[10px] py-[5px]">Рекомендованные</button>
            <button className="text-start w-full px-[10px] py-[5px]">Сначала популярные</button>
          </div>

          <div className="p-[20px] bg-white rounded-[15px] flex flex-col items-start w-full mt-[20px]">
            <button className="text-start w-full px-[10px] py-[5px]">Лайкнутые посты ❤️</button>
            <button className="text-start w-full px-[10px] py-[5px]">Реакции</button>
          </div>
        </div>
      </main>
    </>
  )
}

export default Chat
