import React, { useEffect } from 'react';
import LIKE_SVG from '../assets/svg/heart.svg';
import COMMENT_SVG from '../assets/svg/comment.svg';
import VIEW_SVG from '../assets/svg/view.svg';
import { Link } from 'react-router-dom';

function PostCard({ profile, post }) {
  
  useEffect(() => {
    // console.log(profile);
    console.log(post)
  }, []);
  const dummyUser = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTUsDhVE-Ima-w2-g30KtxeUuFEOaonMbevcg&s';
  

  return (
    <div
      className={`flex flex-col gap-[20px] rounded-[15px] bg-white ${
        profile ? 'bg-[#EBEBEB] rounded-[10px] mt-[10px]' : ''
      }`}
    >
      <Link to={'/'+post?.user?.username} className="flex gap-[20px] pt-[20px] px-[20px] items-center">
        <img
          src={post?.user?.profile_picture?post?.user?.profile_picture:dummyUser}
          className="max-w-[100px] w-full h-full max-h-[100px] object-cover rounded-full"
          alt=""
        />
        <div className="flex flex-col">
          <b>{post?.user?.first_name+' '+post?.user?.last_name}</b>
          <small className="text-gray-500">{post?.created}</small>
        </div>
      </Link>
      <div className="px-[20px]">
        <pre>
          {post?.text}
        </pre>
      </div>
      <div className={profile ? 'p-[10px] rounded-[10px]' : ''}>
        <img
          src={post?.pictures[0].image}
          className={`w-full ${profile ? 'rounded-[10px]' : ''}`}
          alt=""
        />
      </div>

      <div className="flex justify-between items-center pb-[20px] px-[20px]">
        <div className="flex items-center gap-4">
          <button className="flex items-center gap-1">
            <img src={LIKE_SVG} alt="" />
            <p className="text-gray-500">{post?.likes}</p>
          </button>
          <button className="flex items-center gap-1">
            <img src={COMMENT_SVG} alt="" />
            <p className="text-gray-500">31(debug)</p>
          </button>
        </div>
        <div className="flex items-center gap-1 text-gray-500">
          <img src={VIEW_SVG} alt="" />
          <p>{post?.views}</p>
        </div>
      </div>
    </div>
  );
}

export default PostCard;
