import React from 'react'
import PRIVACY_SVG from '../assets/svg/privacy.svg'
function Register() {
  return (
   
<main className=' w-full max-w-[1040px] m-auto mb-[40px]'>
<div className='m-auto max-w-[650px] w-full bg-white p-[40px] rounded-[15px] flex flex-col items-center mt-[40px] outline outline-1 outline-[#FFBE20]'>
        <h1 className=' text-[32px]'>Зарегистрироваться</h1>
        <form action="#" className=' max-w-[440px] w-full mt-[70px] flex flex-col items-end'>
            <input type="email" placeholder='email' className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px]' />
            <input type="text" placeholder='имя пользователя' className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px] mt-[40px]' />
            <input type="password" placeholder='Пароль' className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px] mt-[40px]' />
            <input type="password" placeholder='Повторить пароль' className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px] mt-[40px]' />

            <input type="submit" value="Войти" className=' bg-[#FFBE20] text-[16px] w-full py-[9px] rounded-[10px] mt-[40px]' />
            <div>
                <div className='flex gap-[20px]'>
                    <img src={PRIVACY_SVG} alt="" srcset="" />
            <small className=' mt-[20px] block mx-auto text-[12px]'>Регистрируясь, вы соглашаетесь с 
                  <a href="#" className=' text-[#FFBE20]'> Условиями предоставления услуг</a> и 
                  <a href="#" className=' text-[#FFBE20]'> Политикой конфиденциальности,</a> а также с 
                  <a href="#" className=' text-[#FFBE20]'> Политикой использования файлов cookie.</a> </small>
                </div>
            </div>
          
        </form>

    </div>
</main>
  )
}

export default Register