import React, { useContext, useEffect, useState } from 'react'
import AuthContext from '../contex/AuthContex'
import { useNavigate } from 'react-router-dom'

function Login() {
  const [formData, setFormData] = useState({ username: '', password: '' })

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }
  const navigate = useNavigate()

  const {loginUser, user} = useContext(AuthContext)
  useEffect(()=>{
    if(user){
        navigate('/')
    }
  },[])
  
  return (
     <main className='w-full max-w-[1040px] m-auto mb-[40px]'>
      <div className='m-auto max-w-[650px] w-full bg-white p-[40px] rounded-[15px] flex flex-col items-center mt-[40px] outline outline-1 outline-[#FFBE20]'>
        <h1 className='text-[32px]'>Вход в страницу</h1>
        <form onSubmit={(e)=>loginUser(e)} className='max-w-[440px] w-full mt-[70px] flex flex-col items-end'>
          <input
            type="text"
            name="username"
            placeholder='email, имя пользователя'
            className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px]'
            onChange={handleChange}
          />
          <input
            type="password"
            name="password"
            placeholder='Пароль'
            className='outline outline-1 outline-[#ACA5A5] text-[#ACA5A5] w-full p-[9px] rounded-[10px] mt-[40px]'
            onChange={handleChange}
          />
          <a href="#" className='mt-[20px] block'>Забыли пароль?</a>
          <input type="submit" value="Войти" className='bg-[#FFBE20] text-[16px] w-full py-[9px] rounded-[10px] mt-[40px]' />
          <small className='mt-[20px] block mx-auto'>У вас еще нет аккаунта?</small>
          <a href="#" className='block mx-auto'>Зарегистрироваться</a>
        </form>
      </div>
    </main>
  )
}

export default Login
