import React, { useContext } from 'react'
import SEARCH_SVG from '../assets/svg/search.svg'
import NOTIFICATION_SVG from '../assets/svg/notifications.svg'
import { Link } from 'react-router-dom'
import AuthContext from '../contex/AuthContex'


function Header() {
      const {profile} = useContext(AuthContext)
    
  return (
<>
    <header className=' flex flex-col max-w-[1040px] w-full m-auto'>
        <div className='flex items-center justify-between w-full bg-[#FFBE20]  h-[60px] px-[14px]'>

        <div><h1 className=' font-bold text-[36px] text-white rounded-[2px]'>ExeExe</h1></div>
        <div className=' flex gap-[14px]'>
            <button><img src={SEARCH_SVG} alt="" /></button>
            <button><img src={NOTIFICATION_SVG} alt="" /></button>
        </div>
        </div>
        <nav>
    <ul className='flex justify-between'>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/">Новости</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/friends">Друзья</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/photos">Фотографии</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/login">Войти</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/logout">Выйти</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/Register">Регистрация</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/Chat">Чаты</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to={profile?.username}>Моя страница</Link></li>
        <li className='p-[10px] font-bold text-[18px]'><Link to="/setup">Настройки</Link></li>
    </ul>

</nav>
    </header>

</>



  )
}

export default Header